<template>
  <div class="orderstatics">
    <div class="leftmenu">
      <h3>海外仓订单统计</h3>
      <ul>
        <li
          @click="changeCountry(i)"
          :class="{ active: curIndex === i }"
          v-for="(item, i) in list"
          :key="i"
        >
          {{ item }}
        </li>
      </ul>
    </div>
    <div class="rightmenu">
      <div class="totayOrder">
        <h2>昨日订单量</h2>
        <table>
          <thead>
            <tr>
              <th v-for="(item, i) in todayhead" :key="i">{{ item }}</th>
            </tr>
          </thead>
          <tbody>
            <tr>
              <td v-for="(item, i) in todayList" :key="i">
                {{ item }}
              </td>
            </tr>
          </tbody>
        </table>
      </div>
      <div class="selectOrder" v-if="selectVal !== '全部'">
        <div>
          <h2>{{ selectVal }}昨日订单量</h2>
          <table>
            <thead>
              <tr>
                <td v-for="(item, i) in CurCountryHead" :key="i">{{ item }}</td>
              </tr>
            </thead>
            <tbody>
              <tr>
                <td v-for="(item, i) in CurCountryBody" :key="i">{{ item }}</td>
              </tr>
            </tbody>
          </table>
        </div>
      </div>
      <div
        v-show="showPie"
        class="pieTable"
        ref="pieTable"
        style="width: 80%; height: 500px"
      ></div>
      <div>
        <div class="selectDate">
          <el-select
            v-model="daterange"
            @change="changeDate"
            placeholder="请选择"
          >
            <el-option
              v-for="(item, i) in options"
              :key="i"
              :label="'近' + item + '天'"
              :value="item"
            >
            </el-option>
          </el-select>
        </div>
        <div>
          <table class="m_c">
            <thead>
              <tr>
                <th v-for="(item, i) in orderHead" :key="i">
                  {{ item }}
                </th>
              </tr>
            </thead>
            <tbody>
              <tr v-for="(item, i) in orderBody" :key="i">
                <td>{{ item.warehouse_name }}</td>
                <td>{{ item.tracking_no_avg_minute }}</td>
                <td>{{ item.timeout_num }}</td>
                <td>{{ item.timeout_order_ratio }}</td>
                <td>{{ item.deal_avg_date }}</td>
                <td>{{ item.delivery_avg_date }}</td>
                <td>{{ item.pick_up_avg_date }}</td>
              </tr>
            </tbody>
          </table>
        </div>
      </div>
      <div class="barTable">
        <div class="barlist">
          <span
            @click="changeBarData(i)"
            :class="['barspan', { barActive: curSpanIdx === i }]"
            v-for="(item, i) in this.barLegendData"
            :key="i"
            >{{ item }}</span
          >
        </div>
        <div
          ref="barTable"
          style="width: 80%; height: 500px;margin:0 auto"
        ></div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      showPie: true,
      warehouse_data_list: [],
      date_list: [],
      curIndex: 0,
      curSpanIdx: 0,
      daterange: 7,
      options: [7, 15, 30],
      todayList: [],
      todayOrders: [],
      orderHead: [
        "仓库名称", // warehouse_name
        "获得追踪号的平均时间", // tracking_no_avg_minute
        "超时订单数量", // timeout_num
        "超时订单百分比", //timeout_order_ratio
        "平均处理时间", // deal_avg_date
        "平均出库时间", // delivery_avg_date
        "平均取货时间" // pick_up_avg_date
      ],
      orderBody: [],
      firstRequest: true,
      todayhead: [], // 拿到的国家
      tracing_avg: [], // 追踪号平均时间
      t_a_unit: "", // 单位
      timeout_num: [], // 超时订单数量
      timeout_ratio: [], // 超时订单百分比
      deal_avg: [], // 平均处理时间
      deal_a_unit: "",
      delivery_avg: [], // 平均出库时间
      delivery_a_unit: "",
      pickup_avg: [], // 平均取货时间
      p_u_a_unit: "",
      list: [],
      selectVal: "全部",
      selectCountry: [],
      pieData: {
        legendData: [],
        seriesData: []
      }, // 饼状数据
      barData: {
        xAxisData: [],
        min: 0,
        max: "",
        series: []
      },
      barSeriesData: []
    };
  },
  computed: {
    barLegendData() {
      return [
        `追踪号平均时间(单位:${this.t_a_unit})`,
        `超时订单数量`,
        `超时订单百分比(单位:%)`,
        `平均处理时间(单位:${this.deal_a_unit})`,
        `平均出库时间(单位:${this.delivery_a_unit})`,
        `平均取货时间(单位:${this.p_u_a_unit})`
      ];
    }
  },
  methods: {
    changeBarData(idx) {
      console.log(idx);
      this.curSpanIdx = idx;
      let max;
      switch (idx) {
        case 0:
          this.barData.series[0] = {
            name: `追踪号平均时间(单位:${this.t_a_unit})`,
            type: "bar",
            stack: `追踪号平均时间(单位:${this.t_a_unit})`,
            data: this.tracing_avg,
            label: {
              normal: {
                show: true,
                position: "top"
              },
              formatter: "{@value}"
            }
          };
          // 最大值
          max = Math.max.apply(null, this.tracing_avg);
          this.barData.max = this.ceilNumber(max);
          break;
        case 1:
          this.barData.series[0] = {
            name: "超时订单数量",
            type: "bar",
            stack: "超时订单数量",
            data: this.timeout_num,
            label: {
              normal: {
                show: true,
                position: "top"
              },
              formatter: "{@value}"
            }
          };
          max = Math.max.apply(null, this.timeout_num);
          this.barData.max = this.ceilNumber(max);
          break;
        case 2:
          this.barData.series[0] = {
            name: `超时订单百分比(单位:%)`,
            type: "bar",
            stack: `超时订单百分比(单位:%)`,
            data: this.timeout_ratio,
            label: {
              normal: {
                show: true,
                position: "top"
              },
              formatter: "{@value}"
            }
          };
          max = Math.max.apply(null, this.timeout_ratio);
          this.barData.max = this.ceilNumber(max);
          break;
        case 3:
          this.barData.series[0] = {
            name: `平均处理时间(单位:${this.deal_a_unit})`,
            type: "bar",
            stack: `平均处理时间(单位:${this.deal_a_unit})`,
            data: this.deal_avg,
            label: {
              normal: {
                show: true,
                position: "top"
              },
              formatter: "{@value}"
            }
          };
          max = Math.max.apply(null, this.deal_avg);
          this.barData.max = this.ceilNumber(max);
          break;
        case 4:
          this.barData.series[0] = {
            name: `平均出库时间(单位:${this.delivery_a_unit})`,
            type: "bar",
            stack: `平均出库时间(单位:${this.delivery_a_unit})`,
            data: this.delivery_avg,
            label: {
              normal: {
                show: true,
                position: "top"
              },
              formatter: "{@value}"
            }
          };
          max = Math.max.apply(null, this.delivery_avg);
          this.barData.max = this.ceilNumber(max);
          break;
        case 5:
          this.barData.series[0] = {
            name: `平均取货时间(单位:${this.p_u_a_unit})`,
            type: "bar",
            stack: `平均取货时间(单位:${this.p_u_a_unit})`,
            data: this.pickup_avg,
            label: {
              normal: {
                show: true,
                position: "top"
              },
              formatter: "{@value}"
            }
          };
          max = Math.max.apply(null, this.pickup_avg);
          this.barData.max = this.ceilNumber(max);
          break;
      }
      this.getEchartData2();
    },
    changeDate() {
      let url = "server/order_management/get_warehouse_data/";
      let paramsObj = {};
      paramsObj["days"] = this.daterange;
      if (this.selectVal !== "全部") {
        paramsObj["country"] = this.selectVal;
      }
      this.$axios
        .get(this._url2 + url, {
          params: paramsObj
        })
        .then(res => {
          this.curSpanIdx = 0;
          this.barData.xAxisData = [];
          this.tracing_avg = [];
          this.timeout_num = [];
          this.timeout_ratio = [];
          this.deal_avg = [];
          this.delivery_avg = [];
          this.pickup_avg = [];
          this.orderBody = res.data.warehouse_days_data;
          this.orderBody.forEach(i => {
            this.barData.xAxisData.push(i.warehouse_name);
            this.tracing_avg.push(this.getNum(i.tracking_no_avg_minute).num); // 追踪号平均时间
            this.timeout_num.push(i.timeout_num); // 超时订单数量
            this.timeout_ratio.push(this.getNum(i.timeout_order_ratio).num);
            // 超时订单百分比
            this.deal_avg.push(this.getNum(i.deal_avg_date).num);
            // 平均处理时间
            this.delivery_avg.push(this.getNum(i.delivery_avg_date).num);
            // 平均出库时间
            this.pickup_avg.push(this.getNum(i.pick_up_avg_date).num);
            // 平均取货时间
          });
          this.t_a_unit = this.getNum(
            this.orderBody[0].tracking_no_avg_minute
          ).unit; // 单位
          this.deal_a_unit = this.getNum(this.orderBody[0].deal_avg_date).unit; // 单位
          this.delivery_a_unit = this.getNum(
            this.orderBody[0].delivery_avg_date
          ).unit; //单位
          this.p_u_a_unit = this.getNum(
            this.orderBody[0].pick_up_avg_date
          ).unit; // 单位
          // 最大值，最小值为0
          let max = Math.max.apply(null, this.tracing_avg);
          this.barData.max = this.ceilNumber(max);
          this.barData.series[0] = {
            name: `追踪号平均时间(单位:${this.t_a_unit})`,
            type: "bar",
            stack: `追踪号平均时间(单位:${this.t_a_unit})`,
            data: this.tracing_avg,
            label: {
              normal: {
                show: true,
                position: "top"
              },
              formatter: "{@value}"
            }
          };
          //todo 折线2的数据未处理
          this.getEchartData2();
        });
    },
    changeCountry(val) {
      this.daterange = 7;
      this.curIndex = val;
      this.curSpanIdx = 0;
      this.selectVal = this.list[val];
      // 发请求
      this.getData(this.selectVal);
    },
    getEchartData1() {
      const chart = this.$refs.pieTable;
      let option = {
        title: {
          text: "仓库订单占比",
          left: "center"
        },
        tooltip: {
          trigger: "item",
          formatter: "{a} <br/>{b} : {c} ({d}%)"
        },
        legend: {
          type: "scroll",
          orient: "vertical",
          right: 10,
          top: 20,
          bottom: 20,
          itemWidth: 10,
          itemHeight: 10,
          data: this.pieData.legendData
          // legend中的数据要与series中的name一一对应。
          // 每个name也要与legend中的name意义对应。
        },
        series: [
          {
            name: this.pieData.legendData,
            type: "pie",
            radius: "55%",
            center: ["40%", "50%"],
            data: this.pieData.seriesData,
            emphasis: {
              itemStyle: {
                shadowBlur: 10,
                shadowOffsetX: 0,
                shadowColor: "rgba(0, 0, 0, 0.5)"
              }
            }
          }
        ]
      };
      if (chart) {
        this.pietable = this.$echarts.init(chart);
        this.pietable.setOption(option, true);
        window.addEventListener("resize", () => {
          this.pietable.resize();
        });
      }
      this.$on("hook:destoryed", () => {
        this.pietable.resize();
      });
    },
    getEchartData2() {
      const chart = this.$refs.barTable;
      let option = {
        title: {
          text: "订单信息"
        },
        tooltip: {
          trigger: "axis"
        },
        toolbox: {
          feature: {
            saveAsImage: {}
          }
        },
        xAxis: {
          type: "category",
          data: this.barData.xAxisData,
          axisLabel: {
            interval: 0,
            rotate: 40
          }
        },
        yAxis: {
          type: "value",
          min: this.barData.min,
          max: this.barData.max
        },
        series: this.barData.series
      };
      if (chart) {
        this.barTable = this.$echarts.init(chart);
        this.barTable.setOption(option, true);
        window.addEventListener("resize", () => {
          this.pietable.resize();
        });
      }
      this.$on("hook:destoryed", () => {
        this.pietable.resize();
      });
    },
    getData(country) {
      // 清空饼图和条形图
      this.pieData.legendData = [];
      this.pieData.seriesData = [];
      this.barData.xAxisData = [];
      this.barData.max = "";
      this.barData.series = [];
      this.getEchartData1();
      this.getEchartData2();

      this.tracing_avg = [];
      this.timeout_num = [];
      this.timeout_ratio = [];
      this.deal_avg = [];
      this.delivery_avg = [];
      this.pickup_avg = [];

      let url = "server/order_management/order_statistics/";
      if (country && country !== "全部") {
        url = "server/order_management/order_statistics/?country=" + country;
      }
      this.$axios.get(this._url2 + url).then(res => {
        // 如果不是第一次请求
        if (this.firstRequest) {
          this.list = ["全部", ...res.data.country_data]; // 国家列表
          this.firstRequest = false;
          this.todayOrders = res.data.today_order;
          this.todayOrders.forEach(i => {
            this.todayhead.push(i.country);
            this.todayList.push(i.num);
          });
        }
        this.warehouse_data_list = res.data.warehouse_data_list; // 今日订单
        if (
          !res.data.warehouse_name_list.length ||
          !res.data.warehouse_seven_data.length
        ) {
          this.showPie = false;
        } else {
          this.showPie = true;
        }
        this.pieData.legendData = res.data.warehouse_name_list;
        this.CurCountryHead = [];
        this.CurCountryBody = [];
        this.warehouse_data_list.forEach(i => {
          // 全部仓库
          if (i.warehouse_name !== "订单总数") {
            let obj = {};
            obj["name"] = i.warehouse_name;
            obj["value"] = i.num;
            this.pieData.seriesData.push(obj);
            this.getEchartData1();
          }
          this.CurCountryHead.push(i.warehouse_name);
          this.CurCountryBody.push(i.num);
        });
        this.orderBody = res.data.warehouse_seven_data || [];
        this.orderBody.forEach(i => {
          this.barData.xAxisData.push(i.warehouse_name);
          this.tracing_avg.push(this.getNum(i.tracking_no_avg_minute).num); // 追踪号平均时间
          this.timeout_num.push(i.timeout_num); // 超时订单数量
          this.timeout_ratio.push(this.getNum(i.timeout_order_ratio).num);
          // 超时订单百分比
          this.deal_avg.push(this.getNum(i.deal_avg_date).num);
          // 平均处理时间
          this.delivery_avg.push(this.getNum(i.delivery_avg_date).num);
          // 平均出库时间
          this.pickup_avg.push(this.getNum(i.pick_up_avg_date).num);
          // 平均取货时间
        });
        if (this.orderBody.length) {
          this.t_a_unit = this.getNum(
            this.orderBody[0].tracking_no_avg_minute
          ).unit; // 单位
          this.deal_a_unit = this.getNum(this.orderBody[0].deal_avg_date).unit; // 单位
          this.delivery_a_unit = this.getNum(
            this.orderBody[0].delivery_avg_date
          ).unit; //单位
          this.p_u_a_unit = this.getNum(
            this.orderBody[0].pick_up_avg_date
          ).unit; // 单位
          // 最大值，最小值为0
          let max = Math.max.apply(null, this.tracing_avg);
          this.barData.max = this.ceilNumber(max);
          this.barData.series[0] = {
            name: `追踪号平均时间(单位:${this.t_a_unit})`,
            type: "bar",
            stack: `追踪号平均时间(单位:${this.t_a_unit})`,
            data: this.tracing_avg,
            label: {
              normal: {
                show: true,
                position: "top"
              },
              formatter: "{@value}"
            }
          };
        }

        //todo 折线2的数据未处理
        this.getEchartData2();
      });
    },
    // 待处理取整
    ceilNumber(n) {
      var bite = 0;
      if (n < 10) {
        bite = 1;
      } else {
        while (n >= 1) {
          n /= 10;
          bite += 1;
        }
      }
      return Math.ceil(n) * Math.pow(10, bite);
    },
    // 拆分数字与单位
    getNum(str) {
      let num;
      let unit;
      if (str.indexOf("%") > -1) {
        console.log(str);
        num = Number(str.replace(/%$/g, "")).toFixed(2);
        unit = "%";
      } else {
        num = Number(str.replace(/[\u4e00-\u9fa5]+$/g, ""));
        unit = str.match(/[\u4e00-\u9fa5]+$/g);
      }
      return {
        num,
        unit
      };
    }
  },
  mounted() {
    this.getEchartData1();
    this.getEchartData2();
  },
  created() {
    // 数据获取
    this.getData();
  }
};
</script>

<style scoped>
.orderstatics {
  background: rgba(255, 255, 255, 0.9);
  display: flex;
  width: 100%;
  padding: 30px 20px;
}
.orderstatics .m_c {
  margin: 0 auto;
}
.orderstatics .barlist {
  font-size: 14px;
  /* margin-left: 10px; */
  margin: 20px auto;
  text-align: center;
}
span.barActive {
  color: #fff;
}
.orderstatics .barlist > .barspan {
  margin-right: 20px;
  cursor: pointer;
  background: #ccc;
  padding: 5px 10px;
  display: inline-block;
  border-radius: 5px;
  margin-top: 10px;
}
h3 {
  font-size: 17px;
}
.leftmenu {
  width: 200px;
  height: 100%;
}
.rightmenu {
  padding: 20px 40px;
  flex: 1;
}
.leftmenu li {
  border: 1px solid #e5e5e5;
}
table {
  border-collapse: collapse;
}
table th,
table td {
  font-size: 16px;
  border: 1px solid #e5e5e5;
  padding: 10px;
  text-align: center;
}
.active {
  background-color: #e5e5e5;
}
.selectOrder {
  margin: 30px 0;
}
.selectDate {
  margin-bottom: 20px;
}
</style>



